import React, { useContext } from 'react'
import styled from 'styled-components'
import { MARGIN } from '../constants'
import { AppContext } from '../context'
import { closeButton, upEmoji } from '../img/images'

const Container = styled.div`
    border-radius: 4px;
    margin: 0 ${MARGIN}px;
    padding: ${MARGIN}px 0;
    display: flex;
    background-image: linear-gradient(45deg, #5c6fff 0%, #9345fa 100%);
    font-family: interface;
    font-size: 14px;
    line-height: 19px;
`
const Close = styled.img`
    align-self: flex-start;
    padding: 0 10px;
    margin-left: 20px;
    cursor: pointer;
`

const UpEmoji = styled.img`
    object-fit: contain;
    padding: 0 10px;
    transform: translateY(-25px);
`
const Message = () => {
    const { searchMode, seenMessage, setSeenMessage } = useContext(AppContext)
    // const { term } = useContext(SearchContext)
    const show = !seenMessage && searchMode === 'text'
    return show ? (
        <Container>
            <UpEmoji src={upEmoji} width={25} />
            <div>
                <b>Introducing GIPHY Text</b>
                <div>Give it a try. Just search to create your very own Text Stickers!</div>
            </div>
            <Close src={closeButton} height={17} width={17} onClick={() => setSeenMessage(true)} />
        </Container>
    ) : null
}

export default Message
