import { getGifHeight, getGifWidth } from '@giphy/js-util'
import { SearchContext } from '@giphy/react-components'
import copy from 'copy-text-to-clipboard'
import React, { useContext, useEffect, useState } from 'react'
import { MARGIN } from '../../constants'
import { AppContext, SearchMode } from '../../context'
import { backButton, dndButton, linkButton } from '../../img/images'
import Attribution from './footer'
import {
    AttributionContainer,
    Back,
    Container,
    // ContentContainer,
    CopyButton,
    fadeOutTime,
    GifContainer,
    Header,
    Instructions,
} from './sc'

const MAX_GIF_HEIGHT = 310 // magic number!

const headerTitle: { [key in SearchMode]: string } = { gifs: 'GIF', stickers: 'Sticker', emoji: 'Emoji', text: 'Text' }

const GifDetail = () => {
    const {
        displayGif: gif,
        setDisplayGif,
        searchMode,
        config: { width: appWidth },
    } = useContext(AppContext)

    const { term, activeChannel } = useContext(SearchContext)

    const [copiedAnimation, setCopiedAnimation] = useState<boolean>(false)
    const [isVisible, setIsVisible] = useState<boolean>(false)

    const doCopy = (copyUrl: string) => {
        const copied = copy(copyUrl)
        setCopiedAnimation(copied)
    }

    useEffect(() => {
        const timeout = setTimeout(() => setCopiedAnimation(false), 1000)
        return () => clearTimeout(timeout)
    }, [copiedAnimation])

    const goBack = () => setIsVisible(false)

    useEffect(() => {
        // give the grid a sec to render
        const timeout = setTimeout(goBack, 300)
        return () => clearTimeout(timeout)
    }, [term, activeChannel])

    useEffect(() => {
        // after fade out, clear the gif (gif change isn't enough, if you click the same gif twice)
        if (!isVisible) {
            const timeout = setTimeout(() => setDisplayGif(undefined), fadeOutTime)
            return () => clearTimeout(timeout)
        }
    }, [isVisible, setDisplayGif])
    useEffect(() => {
        if (gif) {
            setIsVisible(true)
        } else {
            goBack()
        }
    }, [gif])
    let width = appWidth - MARGIN * 2
    if (gif) {
        const height = getGifHeight(gif, width)
        if (height > MAX_GIF_HEIGHT) {
            width = getGifWidth(gif, MAX_GIF_HEIGHT)
        }
    }
    return (
        <Container
            isVisible={isVisible}
            isStickerGrid={searchMode === 'stickers' || searchMode === 'text'}
            appWidth={appWidth}
        >
            <Header>
                <Back onClick={goBack} src={backButton} />
                <div>{headerTitle[searchMode]}</div>
            </Header>
            <GifContainer>{gif && <img src={gif.images.original.url} key={gif.id} width={width} />}</GifContainer>
            {gif && (
                <AttributionContainer>
                    <Attribution gif={gif} />
                    {searchMode !== 'text' && (
                        <CopyButton copiedAnimation={copiedAnimation} onClick={() => doCopy(gif.bitly_url)}>
                            <img src={linkButton} />
                        </CopyButton>
                    )}
                </AttributionContainer>
            )}
            <Instructions>
                <img src={dndButton} />
                <b>Drag &amp; Drop</b>&nbsp;into some websites
            </Instructions>
        </Container>
    )
}

export default GifDetail
