import { SearchMode } from './context'

export const SEARCH_HEIGHT = 40
export const MARGIN = 10

export const getAppWidth = (columns: number, gifWidth: number) => columns * (gifWidth + MARGIN) + MARGIN
export type AppConfig = {
    width: number
    height: number
    hasGifDetail: boolean
    columns: number
    flexVertical?: boolean
}

export const Small: AppConfig = {
    width: getAppWidth(2, 145),
    height: 485,
    hasGifDetail: false,
    columns: 2,
}

export const Large: AppConfig = {
    width: getAppWidth(2, 152),
    height: 600,
    hasGifDetail: true,
    columns: 2,
}

export const placeholderCopy: { [key in SearchMode]: string } = {
    gifs: 'GIFs',
    stickers: 'Stickers',
    emoji: 'Emojis',
    text: 'Text',
}
