import { SearchContext } from '@giphy/react-components'
import React, { useContext } from 'react'
import styled from 'styled-components'
import { MARGIN } from '../constants'
import { AppContext } from '../context'
import { closeButton, logo } from '../img/images'
import { TRENDING } from './categories'

const buttonHighlightHeight = 3
const Container = styled.div`
    margin: 0 10px 10px 10px;
    display: flex;
    align-content: center;
    font-size: 18px;
`
const LogoContainer = styled.div`
    flex: 1;
    margin-top: ${MARGIN + buttonHighlightHeight}px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    img {
        cursor: pointer;
    }
`
const Logo = styled.img`
    height: 20px;
`

const Header = () => {
    const { setDisplayGif, setSearchMode, onCloseClick } = useContext(AppContext)
    const { setSearch, setActiveChannel } = useContext(SearchContext)
    const goHome = () => {
        setDisplayGif(undefined)
        setSearch(TRENDING)
        setActiveChannel(undefined)
        setSearchMode('gifs')
    }
    return (
        <Container>
            <LogoContainer>
                <Logo src={logo} onClick={goHome} />
                {onCloseClick && <img src={closeButton} onClick={onCloseClick} />}
            </LogoContainer>
        </Container>
    )
}

export default Header
