const send = (...args: string[]) => {
    // For FIREFOX, ga  is not defined
    ga?.('send', 'event', ...args)
}
export const gaGifClick = (id: string | number) => {
    // yes this is event is called send
    send('Send', 'Click', id as string)
}
export const gaGifDrag = (id: string | number) => {
    // yes this is event is called send
    send('Send', 'Drag', id as string)
}

export const gaAttributionCick = (id: string | number) => {
    send('Attribution', 'Click', id as string)
}

export const gaTagClick = (tag: string) => {
    send('Search', 'Popular Tag Click', tag)
}

export const gaSearch = (term: string) => {
    send('Search', 'Manual', term)
}
